table, th, td {
  border: 1px solid;
}
table{
  margin-left: 15vw;
  margin-top: 15vh;
}

th{
  width: 40px;
  height: 40px;
  font-size: 22px;
}
tr{
  text-align: center;
  width: 40px;
  height: 40px;
  font-size: 22px ;
}
.fill{
  cursor: pointer;
  margin-left: 15vw;
  margin-top: 5vh;
  width: 230px;
  height: 8vh;
  font-size: 30px;
  background-color: #28282b;
  border-radius: 5px;
  border: none;
  color: white;
}
.lock{
  cursor: pointer;
  margin-left: 20vw;
  margin-top: 5vh;
  width: 80px;
  height: 5vh;
  font-size: 30px;
  background-color: blueviolet;
  border: none;
  border-radius: 5px;
  color: white;
}
input[type=button]:disabled {
  background: #dddddd;
  /* color: black; */
}
button:disabled {
  background: #dddddd;
  /* color: black; */
}